.application-process-menu {
  background-color: #eff0f2;
  margin: 0 0 50px 0;
  height: 296px;
  overflow: hidden; }
  @media only screen and (max-width: 892px) {
    .application-process-menu {
      margin: 0 0 10px 0; } }
  .application-process-menu .block-title, .application-process-menu h2 {
    display: none; }
  .application-process-menu ul li {
    text-align: center;
    counter-increment: process-counter;
    width: 198px;
    padding: 0;
    float: left;
    background: transparent url("../images/application-process-bar.png") center center no-repeat;
    background-position: 0px 116px;
    background-repeat: repeat-x;
    /*opacity:0.4;*/ }
    .application-process-menu ul li:first-child {
      background-position: 100px 116px;
      background-repeat: no-repeat; }
    .application-process-menu ul li:last-child {
      background-position: -100px 116px;
      background-repeat: no-repeat; }
    .application-process-menu ul li a {
      height: 297px;
      padding: 177px 20px 0;
      display: block;
      color: black;
      background: transparent url("../images/dummy-image.png") center center no-repeat;
      background-size: 128px auto !important;
      background-position: center 40px !important; }
      .application-process-menu ul li a::before {
        font-size: 34px;
        font-weight: 600;
        display: block;
        content: counter(process-counter); }
    .application-process-menu ul li.active {
      background-color: rgba(255, 255, 255, 0.4);
      opacity: 1; }
      .application-process-menu ul li.active a::before {
        color: #0d252c; }
    .application-process-menu ul li.slick-current, .application-process-menu ul li:hover, .application-process-menu ul li:active, .application-process-menu ul li:focus {
      /*opacity:1;*/ }
    .application-process-menu ul li:nth-child(8) a {
      background: transparent url("../images/application-process/_generic/8.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(7) a {
      background: transparent url("../images/application-process/_generic/7.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(6) a {
      background: transparent url("../images/application-process/_generic/6.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(5) a {
      background: transparent url("../images/application-process/_generic/5.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(4) a {
      background: transparent url("../images/application-process/_generic/4.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(3) a {
      background: transparent url("../images/application-process/_generic/3.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(2) a {
      background: transparent url("../images/application-process/_generic/2.png") center center no-repeat; }
    .application-process-menu ul li:nth-child(1) a {
      background: transparent url("../images/application-process/_generic/1.png") center center no-repeat; }
  .application-process-menu ul .slick-arrow {
    margin-top: -31px;
    z-index: 100; }
    @media only screen and (max-width: 892px) {
      .application-process-menu ul .slick-arrow {
        display: none !important; } }

