// views, not ip-toolkit case studies, mediation referral, or qualified persons
#block-views-publications-reports-block-2,
.page-about-us-careers-current-vacancies-listing,
.page-about-us-news-and-community-blog,
.page-about-us-news-and-community-events,
.page-about-us-news-and-community-official-notice,
.page-tools-resources-case-studies,
.page-tools-resources-journals-pbr-journals,
.page-tools-resources-publications-reports,
.page-tools-resources-journals-patent-journals,
.page-tools-resources-video,
.view-id-news,
.view-id-trade_mark_certification_rules,
.view-id-education {
    .views-row {
        background: $color_athens_gray_approx;
        margin-bottom: 10px;
        .panel-col-first {
            width: 23%;
            img {
                margin: 30px;
                width: 140px;
                height: 140px;
                @media only screen and (max-width: $break-point-xlarge + 200) {
                    margin: 5px;
                    width: 100%;
                    height: auto;
                }
            }
            @media only screen and (max-width: $break-point-small) {
                display: none;
            }
        }
        .panel-col-last {
            padding-top: 22px;
            width: 70%;
            @media only screen and (max-width: $break-point-small) {
                width: 100%;
            }
            .views-field-field-date {
                color: $color_mountain_mist_approx;
                margin-bottom: 10px;
            }
        }
        .date-display-single {
            color: $color_mountain_mist_approx;
        }
        .views-field-title a {
            color: $color_firefly_approx;
            font-size: 24px;
            font-weight: bold;
            text-decoration: underline;
        }
    }
    .views-field-field-date {
        color: $color_mountain_mist_approx;
        margin-bottom: 10px;
    }
    .views-field-field-tags { /* */
        ul {
            list-style: none;
            padding: 0;
            margin-top: 10px;
            li {
                display: inline-block;
                background: $charcoal;
                color: $white;
                padding: 5px;
                margin: 0px 0px 0px 5px;
            }
        }
    }
}

// adds charcoal coloured tags across the views.
.views-field-field-tags {
    ul {
        list-style: none;
        padding: 0;
        margin-top: 10px;
        li {
            display: inline-block;
            background: $charcoal;
            color: $white;
            padding: 5px;
            margin: 0px 0px 0px 5px;
        }
    }
}

